.tmd-table-wrapper {
  overflow-x: auto;
  th, td {
    padding: 0 8px;
  }
}
.tmd-table {
  table-layout: auto;
}
.tmd-table-header {
  display: inline-block;
  width: calc(100% - 80px);
}
.tmd-table-header_row {
  th:first-of-type, th:nth-of-type(2) {
    width: 1px;
  }
}
@media (max-width: 1200px) {
  .content-strip--has-table {
    justify-content: left;
    .flex-item {
      width: 100%;
    }
  }
}
@media (max-width: 420px) {
  .content-strip--has-table > .flex-item > .tmd-table-wrapper > .tmd-table-header {
    line-height: 1 !important;
    height: 64px;
  }
}
@media print {
  .topnav, .content-strip.content-strip--primary.content-strip--extra-padding.content-strip--center, .mdc-icon-button {
    display: none;
  }
  .content-strip {
    padding: 0;
    margin: 0;
    background: #ffffff;
  }
  .page-content {
    height: auto;
  }
  .tmd-table-wrapper {
    border: 1px solid rgba(0,0,0,0.2);
    margin: 0;
  }
  .tmd-table-wrapper .tmd-table-header {
    color: rgba(0,0,0,0.87);
  }
  .tmd-table-header_row {
    border-bottom: 1px solid rgba(0,0,0,0.1);
    color: rgba(0,0,0,0.6);
  }
  .tmd-table-row {
    border-bottom: 1px solid rgba(0,0,0,0.1);
    color: rgba(0,0,0,0.87);
  }
}
